* {
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  color: #393c41;
  margin: 0;
  padding: 0;
  font-size: 1rem !important;
}

a{
  text-decoration: none;
}

body {
  width: 100%;
  overflow-x:hidden ;
  overflow-y: visible;
}

@keyframes animateDown {

  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(5px);
  }

  60% {
    transform: translateY(3px);
  }
}